import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import FormLayout from '@vkontakte/vkui/dist/components/FormLayout/FormLayout';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import connect from '@vkontakte/vk-connect';

import profile from '../img/profile.png';
import top from '../img/top.png';
import shop from '../img/shop.png';

import openSocket from 'socket.io-client';

class Home extends React.Component {
	
	constructor(props) {
		super(props);
	
		this.state = {
			activePanel: 'main',
			popout: null
		};

		this.login = this.login.bind(this);
		this.componentDidMount = this.componentDidMount.bind(this);
		
		connect.send("VKWebAppInit", {});
	}
	
	componentDidMount () {
		this.setState({ popout: <ScreenSpinner /> });
		setTimeout(() => { this.login() }, 100);
	}
	
	login() {
		const socket = openSocket.connect('https://ih1705413.vds.myihor.ru:8080/', {secure: true});
		socket.on('connect', function () {
			socket.json.send({'method': 'login','url': window.location.search.replace('?','')});
			socket.on('message', function (msg) {
				if(msg.method === 'login'){
					if(msg.access){
						this.setState({ popout: null });
					}
				}
				console.log(msg);
			}.bind(this));
		}.bind(this));
	}

	render() {

		return (
			<View popout={this.state.popout}>
				<View activePanel={this.state.activePanel}>
					<Panel id="main">
						<PanelHeader>VK App</PanelHeader>
						<Div style={{display: 'flex'}}>
							<Group>
								<FormLayout>
									<Button stretched style={{height: "75px", borderRadius: "22px", boxShadow: "0 0px 30px 0 rgba(0, 0, 0, 0.3)", marginLeft: "12px", marginTop: "36px", backgroundColor: "#2196f3"}}>
										<img src={profile} style={{height: "55px"}}/>
									</Button>
									Профиль
								</FormLayout>
								<FormLayout>
									<Button stretched style={{height: "75px", borderRadius: "22px", boxShadow: "0 0px 30px 0 rgba(0, 0, 0, 0.3)", marginLeft: "12px", marginTop: "36px", backgroundColor: "#00bcd4"}}>
										<img src={top} style={{height: "55px"}}/>
									</Button>
									Рейтинг
								</FormLayout>
								<FormLayout>
									<Button stretched style={{height: "55px", borderRadius: "22px", boxShadow: "0 0px 30px 0 rgba(0, 0, 0, 0.3)", marginLeft: "12px", marginRight: "12px", marginTop: "36px", backgroundColor: "#03a9f4"}}>
										<img src={shop} style={{height: "70px"}}/>
									</Button>
									Магазин
								</FormLayout>
							</Group>
						</Div>
					</Panel>
				</View>
			</View>
		);
	}
}

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Home;
